body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.navbar {
    background: #2883FF;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

.navbar a {
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    text-decoration: none;
    margin: 0 20px;
}

.navbar a img {
    width: 40px;
    height: 40px;
    padding: 5px;
}

.cslr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.text-section,
.image-section {
    flex: 1;
    padding: 20px;
    box-sizing: border-box;
}

.button {
    background: #2883FF;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.card img {
    width: 149px;
    height: 149px;
}

.tv-section {
    margin: 50px 0;
}

.tv-section button {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
}

.ftco-gallery {
    background-image: url('./images/subs.png');
    background-size: cover;
    background-position: center;
    padding: 20px; /* Add padding for spacing around the form */
}

/* Style the input type box */
.ftco-gallery .form-control {
    border-radius: 8px; /* Add border-radius for rectangular corners */
}

.ftco-footer {
    background-image: url('./images/footer.png');
    background-size: cover;
    background-position: center;
    color: #ffffff;
    padding: 50px 0;
    text-align: center;
}

@media (max-width: 767.98px) {
    .cslr {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar a {
        margin: 10px 0;
    }

    .text-section,
    .image-section {
        padding: 10px;
    }

    .tv-section button {
        width: 100%;
        margin: 10px 0;
    }
}


.custom-carousel {
    max-width: 1920px; /* Set the maximum width for the carousel */
    max-height: 1183px; /* Set the maximum height for the carousel */
    overflow: hidden; /* Hide the overflow content if the images exceed the specified dimensions */
    position: relative; /* Set position to relative for containing absolutely positioned elements */
}

.custom-carousel .carousel-inner {
    text-align: left; /* Align text to the left within the carousel */
}

.custom-carousel .carousel-item {
    position: relative;
    max-width: 100%; /* Ensure images don't exceed the container width */
    max-height: 100%; /* Ensure images don't exceed the container height */
}

.custom-carousel .carousel-caption {
    position: absolute;
    top: 20px; /* Adjust the top position of the text */
    left: 20px; /* Adjust the left position of the text */
    text-align: left; /* Align text to the left */
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background for better visibility */
    padding: 20px; /* Add padding to the caption */
    border-radius: 8px; /* Add border-radius for rounded corners */
    color: #FFFFFF; /* Text color */
}

/* Optional: Hide the carousel control buttons */
.custom-carousel .carousel-control-prev,
.custom-carousel .carousel-control-next {
    display: none;
}
